.messenger {
  display: grid;
  width: 100%;
  height: 100vh;
  background: #eeeef1;

  grid-template-columns: 23% 56% 30%;
  grid-template-rows: 60px auto 60px;
  grid-column-gap: 1px;
  grid-row-gap: 1px;
}

.container {
  padding: 10px;
}

.scrollable {
  position: relative;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.sidebar {
  background: white;
  grid-row-start: 1;
  grid-row-end: span 3;
}

.content {
  background: white;
  grid-row-start: 1;
  grid-row-end: span 3;
  min-width: 100%;
}

.right-component {
  background: white;
  overflow-y: hidden;
  max-height: 100%;
  grid-row-start: 1;
  max-width: -webkit-min-content;
  max-width: -moz-min-content;
  max-width: min-content;
  grid-row-end: span 3;
}

.footer {
  grid-column-start: 2;
  background: white;
}

